import React from 'react';
import { NavLink } from "react-router-dom";

import './List.css';

const List = props => {
    console.log(props);
    return (
        <ul className='List'>
            {props.content.map(el => {
                return <li key={el.url}><NavLink end to={el.url}>{el.desc}</NavLink></li>
            })}
        </ul>
    );
};

export default List;