import React from 'react';
import { Provider } from "react-redux";
import { createStore, applyMiddleware, compose, combineReducers } from "redux";
import thunk from "redux-thunk";
import { createRoot } from 'react-dom/client';
import './styles/index.scss';
import App from './App';
import reportWebVitals from './reportWebVitals';
import wordsConstructorReducer from "./js/react_modules/store/reducers/wordsConstructorReducer";
import sentencesConstructorReducer from "./js/react_modules/store/reducers/sentencesConstructorReducer";
import authReducer from "./js/react_modules/store/reducers/auth";

const root = createRoot(document.getElementById('root'));
const rootReducer = combineReducers({
  wordsConstructor: wordsConstructorReducer,
  auth: authReducer,
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(
 rootReducer,
 composeEnhancers(applyMiddleware(thunk))
);

root.render(
  <React.StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();