import * as actionTypes from "./actionTypes";
import axios from "axios";

export const fetchStart = () => {
  return {
    type: actionTypes.FETCH_START
  };
};

export const fetchSuccess = (data, options = {}) => {
  const { bare } = options;

  return {
    type: actionTypes.FETCH_SUCCESS,
    data,
    bare,
  };
};

export const fetchFail = error => {
  return {
    type: actionTypes.FETCH_FAIL,
    error,
  };
};

export const asyncFetchStart = (url, options = {}) => {
  const { bare } = options;

  return dispatch => {
    dispatch(fetchStart());
    axios
      .get(url)
      .then(res => dispatch(fetchSuccess(res.data, { bare })))
      .catch(err => dispatch(fetchFail(err.response)));
  };
};

export const clearData = category => {
  return {
    type: actionTypes.CLEAR_DATA,
    category: category
  };
};
