import React, { useState, useEffect, Fragment } from "react";
import { connect } from "react-redux";
import { useParams } from "react-router-dom";

import * as actionCreators from "../../store/actions/index";
import Toolbar from "../../components/Navigation/Toolbar/Toolbar";
import SideDrawer from "../../components/Navigation/SideDrawer/SideDrawer";
import "./Layout.css";

const Layout = (props) => {
  const [sideDrawerIsOpen, setSideDrawerIsOpen] = useState(false);
  const { onAuth, children } = props;
  const { isAuth } = useParams();

  useEffect(() => {
    onAuth();
  }, []);

  const toggleSideDrawerHandler = (e) => {
    setSideDrawerIsOpen((prevState) => !prevState);
  };

  return (
    <Fragment>
      <header>
        <SideDrawer
          isAuth={props.isAuth}
          click={toggleSideDrawerHandler}
          isOpen={sideDrawerIsOpen}
        />
        <Toolbar isAuth={isAuth} />
      </header>
      <main>{children}</main>
      <footer />
    </Fragment>
  );
}

const mapStateToProps = state => {
  return {
    isAuth: state.auth.token
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onAuth: () => dispatch(actionCreators.authStateCheck())
  };
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
  )(Layout);
