import React, {Component} from 'react';
import {connect} from 'react-redux';

import Spinner from '../../components/UI/Spinner/Spinner';
import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import * as actionCreators from '../../store/actions/index';
import { getSpreadSheetUrl, getTaskPathByPageId } from '../../../constants'; 

import './FillGaps.css';

class FillGaps extends Component {
    state = {
        data: null,
        form: [
            {
                elementType: "input",
                elementConfig: {
                  type: "text",
                  maxLength: 20,
                  autoComplete: 'off'
                },
                value: "",
                touched: false,
                correct: false
            }
        ],
        controls: [
            {
                name: 'ShowScore'
            },
            {
                name: 'ShowAnswers'
            },
            {
                name: 'Restart',
                type: 'reset'
            }
        ]
    };

    refSentense = React.createRef();

    componentDidMount() {
        this.props.fetchData(
            getSpreadSheetUrl(getTaskPathByPageId(11))
        );
    }

    componentDidUpdate(prevProps, prevState) {
		if(prevProps && prevProps !== this.props) {
            if(!this.props.data) return;
            if(!this.props.data[0].opposite) return;
            const updatedForm = [];
            this.props.data.forEach(el => {
                const inputElem = {...this.state.form[0]};
                const config = {...inputElem.elementConfig, name: el.opposite.toLowerCase()};
                inputElem.elementConfig = config;
                updatedForm.push(inputElem);
            });
			this.setState({data: this.props.data, form: updatedForm});
		}
    }
    
    onSubmitHandler = e => {
        e.preventDefault();
    };

    onInputHandler = (e, id) => {
        const updatedForm = [...this.state.form];
        updatedForm[id].value = e.target.value.toLowerCase();
        if(e.target.value.toLowerCase() === e.target.name) {
            updatedForm[id].correct = true; 
        }else {
            updatedForm[id].correct = false; 
        }
        this.setState({form: updatedForm});
    };

    showScoreHandler = () => {
        let totalCorrect = this.state.form.filter(el => el.correct).length;
        alert(`Your score is ${totalCorrect}/${this.state.form.length}`);
        this.showAnswersHandler();
    };

    showAnswersHandler = () => {
        Array.from(this.refSentense.current.children).forEach((el,index) => {
            if(this.state.form[index].correct) {
                el.classList.add('-correct');
            }else {
                el.classList.add('-wrong');
            }
        });
    };

    showCorrectAnswersHandler = () => {
        const updatedForm = [...this.state.form];
        updatedForm.forEach(el => el.value = el.elementConfig.name);
        this.setState({form: updatedForm});
    };

    onResetHandler = () => {
        let updatedForm = [...this.state.form];
        updatedForm.forEach(el => {
            el.value = '';
            if(el.correct) {
                el.correct = false;
            }
        });
        Array.from(this.refSentense.current.children).forEach((el, index) => {
            el.classList.remove('-correct', '-wrong')
        });
        this.setState({form: updatedForm});
    };

    render() {
        console.log('Props: ', this.props);
        let data = null;
        if(this.props.loading) {
            data = <Spinner />;
        }
        if(this.state.data) {
            data = (
                <div className='FillGaps'>
                    <form onSubmit={this.onSubmitHandler}>
                        <ol ref={this.refSentense}>
                            {this.state.data.map((el, index) => (
                                <li key={el.opposite + index}>
                                    {el.word.split('|+|')[0]}
                                    <Input data={this.state.form[index].elementConfig} value={this.state.form[index].value} change={(e) => this.onInputHandler(e, index)} />
                                    {el.word.split('|+|')[1]}
                                </li>
                            ))}
                        </ol>
                        <div className='FillGapsControls'>
                            {
                                this.state.controls.map(el => (
                                    <Button classes='Plain' 
                                        {...el} 
                                        key={el.name}
                                        click={
                                            el.name === 'ShowScore'
                                            ? this.showScoreHandler :
                                            el.name === 'ShowAnswers'
                                            ? this.showCorrectAnswersHandler
                                            : this.onResetHandler
                                        }
                                    >{el.name}</Button>
                                ))
                            }
                        </div>
                        <div className='Total'>
                            
                        </div>
                    </form>
                </div>
            );
        }
        return data;
    }
}

const mapStateToProps = state => {
    return {
        data: state.wordsConstructor.fetchedData,
		loading: state.wordsConstructor.loading,
		error: state.wordsConstructor.error
    };
};

const mapDispatchToProps = dispatch => {
    return {
        fetchData: url => dispatch(actionCreators.asyncFetchStart(url))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(FillGaps);