import React, {Fragment, useState, useEfffect, useEffect} from 'react';
import {NavLink} from 'react-router-dom';
import { getSpreadSheetUrl } from '../../../constants';

import axios from 'axios';

import Button from '../UI/Button/Button';
import FillGaps from '../../containers/FillGaps/FillGaps';
import {processJSONfromGoogleSpreadsheet as processJSON} from '../../utilities/utilities';

import './Video.css';

const Video = props => {
    let [state, setState] = useState(null);
    useEffect(() => {
        if(!props.quotes) return;
        axios
        .get(getSpreadSheetUrl('10. Quotes'))
        .then(res => setState(processJSON(res.data)));
    }, []);

    let data = null;
    if(state) {
        data = state.map(el => (
            <div className='QuoteContainer' key={el.opposite}>
                <div className='Quote'>
                    <p>{el.word}</p>
                    <p className='author'>{el.opposite}</p>
                    <hr/>
                </div>
            </div>
        ));
    }
    if(props.mainTask) {
        data = <FillGaps />
    }

    return (
        <Fragment>
            <div className='Video'>
                <div>
                    <iframe title={props.desc} src={props.url} width="854" height="480" frameBorder="0" scrolling="no" allowFullScreen></iframe>
                </div>
            </div>
            {data}
            <NavLink end to={props.taskLink || '/'+ props.desc.toLowerCase().split(' ').join('-') +'-task'} className='TaskLink'>Hop to Task</NavLink>
        </Fragment>
    );
};

export default Video;