import React from 'react';
import './RitesOfPassage.css';

const Task2 = props => {
    return (
        <div className='Task2'>
            <table>
			<tbody>
				<tr>
					<td colSpan="3" className="task-description">
						For questions 1-12, answer by matching the opinions expressed with the people
listed (A-E) on the right below.
Some of the choices may be required more than once.
Note: When more than one answer is required, these may be given in any order.
					</td>
				</tr>
				<tr>
					<td colSpan="2">Which person</td>
					<td></td>
				</tr>
				<tr>
					<td>stopped work after a short time?</td>
					<td>1....</td>
					<td rowSpan="12">
						<ol className="test-people">
							<li>Matthew Bashford</li>
							<li>Mike Hale</li>
							<li>Pete Fulford</li>
							<li>Kelly Moore</li>
							<li>Rebecca Jones</li>
						</ol>
					</td>
				</tr>
				<tr>
					<td>has a full-time temporary job?</td>
					<td>2....</td>
				</tr>
				<tr>
					<td>had to do something unwillingly because of a poor financial situation?</td>
					<td>3....</td>
				</tr>
				<tr>
					<td>misses friends on the course?</td>
					<td>4....     5....</td>
				</tr>
				<tr>
					<td>has not left university yet?</td>
					<td>6....</td>
				</tr>
				<tr>
					<td>finds an alternative to a job with a salary?</td>
					<td>7....</td>
				</tr>
				<tr>
					<td>feels confident about finding a job?</td>
					<td>8....</td>
				</tr>
				<tr>
					<td>found it difficult to get on with colleagues?</td>
					<td>9....</td>
				</tr>
				<tr>
					<td>is not very ambitious with regard to future jobs?</td>
					<td>10....</td>
				</tr>
				<tr>
					<td>found a job easily?</td>
					<td>11....</td>
				</tr>
				<tr>
					<td>learnt a lot from a job?</td>
					<td>12....</td>
				</tr>
			</tbody>
		</table>
        </div>  
    );
};

export default Task2;